/* Import Raleway font to match other pages */
@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;500;600;700&display=swap');

.privacy-page {
  font-family: 'Raleway', sans-serif;
  color: #333;
  line-height: 1.6;
}

.privacy-hero {
  position: relative;
  height: 250px;
  background-image: linear-gradient(135deg, #4CAF50, #2E7D32);
  background-size: cover;
  background-position: center;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 3rem;
}

.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: 0;
}

.privacy-hero h1 {
  font-size: 3rem;
  font-family: 'Raleway', sans-serif;
  font-weight: 700;
  color: #eaffeb;
  text-align: center;
  position: relative;
  z-index: 1;
}

.privacy-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 2rem 2rem 5rem;
}

.privacy-section {
  margin-bottom: 4rem;
  padding-bottom: 2rem;
  border-bottom: 1px solid #e0e0e0;
}

.privacy-section:last-child {
  border-bottom: none;
  margin-bottom: 0;
}

.privacy-section h2 {
  font-size: 1.8rem;
  color: #2E7D32;
  margin-bottom: 1.8rem;
  position: relative;
  padding-bottom: 12px;
}

.privacy-section h2:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 60px;
  height: 3px;
  background-color: #4CAF50;
}

.privacy-section p {
  margin-bottom: 1.2rem;
  line-height: 1.7;
}

.privacy-list {
  padding-left: 1.8rem;
  margin-bottom: 1.8rem;
  margin-top: 1.2rem;
}

.privacy-list li {
  margin-bottom: 0.8rem;
  padding-left: 0.5rem;
}

.privacy-contact-info {
  background-color: #f5f5f5;
  padding: 2rem;
  border-radius: 8px;
  margin-top: 1.5rem;
}

.privacy-contact-info p {
  margin-bottom: 0.8rem;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .privacy-hero {
    height: 180px;
  }
  
  .privacy-hero h1 {
    font-size: 2.5rem;
  }
  
  .privacy-section h2 {
    font-size: 1.6rem;
    margin-bottom: 1.5rem;
  }
  
  .privacy-container {
    padding: 1.5rem 1.5rem 4rem;
  }
  
  .privacy-section {
    margin-bottom: 3rem;
    padding-bottom: 1.5rem;
  }
}

@media (max-width: 480px) {
  .privacy-hero h1 {
    font-size: 2rem;
  }
  
  .privacy-section {
    margin-bottom: 2.5rem;
    padding-bottom: 1.5rem;
  }
  
  .privacy-section h2 {
    font-size: 1.4rem;
    margin-bottom: 1.2rem;
  }
  
  .privacy-container {
    padding: 1rem 1.2rem 3rem;
  }
  
  .contact-info {
    padding: 1.5rem;
  }
}
